* {
    box-sizing: border-box;
    z-index: 100;
}

// переменные
// var(--color-blue); - пример для использования

:root {
    --color-white: #fff;
    --color-violet: #4c328c;
    --color-pink: #dc4c94;
    --color-blue: #10069F;
    --main-font: 'Montserrat', sans-serif; // шрифт по умолчанию
    --main-color: #2c2c2c; // цвет по умолчанию
    --main-font-size: 16px; // размер шрифта по умолчанию
    --color-grey: #a6a6a6;
    --color-orange: #FF7500;
}
a {
    text-decoration: none !important;
    color: inherit;
}

.hidden {
    display: none;
}

button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;

    &:focus {
        outline: none;
    }
}

.plus_button {
    display: none;
    background: var(--color-orange);
    color: var(--color-white);
    font-weight: bold;
    font-size: 13px;
    padding: 12px 20px;
    border-radius: 24px;
    //max-width: 137px;
    margin: 0 auto 0;
}

html {
    background-color: var(--color-white);
}
.wrapper {
    // position: absolute;
    // top: 0;
    // left: 0;
    // right: 0;
    // bottom: 0;
    overflow-x: hidden;
    // overflow-y: scroll;
    // -webkit-overflow-scrolling: touch;
    background: var(--color-white);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
body {
    font-family: var(--main-font), sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: var(--main-font-size);
    color: var(--main-color);
}
img {
    max-width: 100%;
    display: block;
}
h1,
h2,
h3,
h4,
p {
    margin: 0;
}

.mob-scroll {
    overflow-x: scroll;
}

.section {
    max-width: 100%;
    width: 100%;
    position: relative;
    padding: 0 16px;

    &__content {
        max-width: 1200px;
        margin: 0 auto;
    }
}

.game {
    width: 600px;
    height: 200px;
    border-bottom: 1px solid #000;
    margin: auto;
}

#dino {
    width: 50px;
    height: 50px;
    background: url(../../public/dino.png);
    background-size: 50px 50px;
    position: relative;
    top: 150px;
}

#cactus {
    width: 20px;
    height: 40px;
    background-image: url(../../public/cactus.png);
    background-size: 20px 40px;
    position: relative;
    top: 110px;
    left: 580px;
    animation: cactusMov 1s infinite linear;
}

@keyframes cactusMov {
  0% {
      left: 580px;
  } 
  100% {
      left: -20px;
  }  
}

.jump {
    animation: jump 0.3s linear;
}

@keyframes jump {
    0% {
        top: 150px;
    }
    30% {
        top: 130px;
    } 
    50% {
        top: 80px;
    }
    80% {
        top: 130px;
    }
    100% {
        top: 150px;
    }  
  }

